<template>
  <div class="seller" v-el:seller>
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc border-1px">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}元</span>
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}元</span>
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}分钟</span>
            </div>
          </li>
        </ul>
        <div class="favorite">
          <span class="icon-favorite" ></span>
          <span class="text"></span>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper border-1px">
          <p class="content">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item border-1px" v-for="item in seller.supports">
            <span class="icon" :class="classMap[seller.supports[$index].type]"></span>
            <span class="text">{{seller.supports[$index].description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" v-el:pic-wrapper>
          <ul class="pic-list" v-el:pic-list>
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h1 class="title border-1px">商家信息</h1>
        <ul >
          <li class="info-item" v-for="info in seller.infos">{{seller.infos}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from '../../components/star/star.vue';
  import BScroll from 'better-scroll';
  import split from '../../components/split/split.vue';
export default {
    props: {
        seller: {
            type: Object
        }
    },
    components: {
        star,
        split
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    watch: {
      'seller' () {
          this._initScroll();
          this._initPics();
      }
    },
    ready() {
      this._initScroll();
      this._initPics();
    },
    methods: {
        _initScroll() {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$els.seller, {
                click: true
              });
            } else {
                this.scroll.refresh();
            }
        },
        _initPics() {
          if (this.seller.pics) {
            let picWidth = 120;
            let margin = 6;
            let width = (picWidth + margin) * this.seller.pics.length - margin;
            this.$els.picList.style.width = width;
            this.$nextTick(() => {
                if (!this.picScroll) {
                  this.picScroll = new BScroll(this.$els.picWrapper, {
                    scrollX: true,
                    eventPassthrough: 'vertical'
                  });
                } else {
                   this.picScroll.refresh();
                }
            });
          }
        }
    }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/style/mixin.styl"
  .seller
    position :absolute
    top: 174px
    width :100%
    left :0
    bottom :0
    overflow :hidden
    .overview
      padding :18px
      .title
        margin-bottom :8px
        font-size :14px
        line-height :14px
        color :rgb(7, 17, 27)
      .desc
        padding-bottom :18px
        font-size :0
        border-1px(rgba(7, 17, 27, 0.1))
        .star
          display :inline-block
          vertical-align :top
          margin-right :8px
        .text
          display :inline-block
          line-height :18px
          vertical-align :top
          font-size :10px
          color :rgb(77, 85, 93)
          margin-right :12px
      .remark
        display :flex
        padding-top :18px
        .block
          flex :1
          border-right :1px solid rgba(7, 17, 27, 0.1)
          text-align :center
          &:last-child
            border-right : none
          h2
            margin-bottom :4px
            line-height :10px
            color :rgb(147, 153, 159)
            font-size :10px
          .content
            line-height :24px
            color :rgb(7, 17, 27)
            font-size :10px
            .stress
              font-size :24px
    .bulletin
      padding :18px 18px 0 18px
      .title
        margin-bottom :8px
        font-size :14px
        line-height :14px
        color :rgb(7, 17, 27)
      .content-wrapper
        padding :0 12px 16px 12px
        border-1px(rgba(7, 17, 27, 0.1))
        .content
          line-height :24px
          font-size :12px
          color :rgb(240, 20, 20)
      .supports
        .support-item
          padding :16px 12px
          border-1px(rgba(7, 17, 27, 0.1))
          font-size :0
          &:last-child
            border-bottom :none
          .icon
            display :inline-block
            vertical-align :top
            width :16px
            height :16px
            margin-right :6px
            background-size :16px 16px
            background-repeat :no-repeat
            &.decrease
              bg-image('decrease_4')
            &.discount
              bg-image('discount_4')
            &.guarantee
              bg-image('guarantee_4')
            &.invoice
              bg-image('invoice_4')
            &.special
              bg-image('special_4')
          .text
            font-size :12px
            line-height :12px

    .pics
      padding :18px
      .title
        margin-bottom :12px
        font-size :14px
        line-height :14px
        color :rgb(7, 17, 27)
      .pic-wrapper
        width :100%
        overflow :hidden
        white-space :nowrap
        .pic-list
          font-size :0
          .pic-item
            display :inline-block
            margin-right :6px
            height :90px
            width :120px
            &:last-child
              margin :0
    .info
      padding :18px 18px 0 18px
      .title
        padding-bottom :12px
        font-size :14px
        border-1px(rgba(7, 17, 27, 0.1))
        line-height :14px
        color :rgb(7, 17, 27)
      .info-item
        padding :16px 12px
        line-height :16px
        color :rgb(7, 17, 27)
        font-size :12px
        border-1px(rgba(7, 17, 27, 0.1))
        &:last-child
          border-bottom :none
  </style>



